<template>
    <div class="main">

        <!--固件功能图区域开始 -->
        <div class="firmware-function-diagram">
            <div class="steps">
                <el-steps :active="active" finish-status="success">
                    <el-step v-for="(item, index) in stepsList" :title="item.name" @click.native="activeClick(item)"
                        :key="index"></el-step>
                </el-steps>
            </div>
        </div>
        <!-- 固件功能图区域结束 -->

        <!-- 固件图片数据展示开始 -->
        <div class="firmware_Product">
            <div class="firmware_Product-Data" v-if="active == 1">
                <div class="firmware_Product-Data-box1">
                    <div class="firmware_Product-Data-list" v-for="(item, index) in firmwareProductList2" :key="index"
                        @click="platformClick(item)">
                        <div><img :src="imageSrc(item.imgBase64)" alt=""></div>
                        <div class="firmware_Product-Data-list-item">{{ item.title }}</div>
                    </div>
                </div>
            </div>
        </div>
        <!-- 固件图片数据展示开始 -->
        <div class="container">
            <div @click="goTop" v-show="isShowGoTopButton">
                <img src="@/assets/home/runterTop.png" alt="" class="runter-top">
            </div>
        </div>
    </div>
</template>
<script>
import { getFirmware, getFirmwareProduct, getFlashListiPhone, getFlashListiPad, getFlashListiPod, getFlashListMac, getinfoDataList, getFlashListAppleTV, getFlashListWatch, getFlashListHomePod, getFlashListVision } from '../../api/firmware.js'
import { mapState } from 'vuex';
export default {

    data() {
        return {
            active:1,
            isShowGoTopButton: false,
            stepsList: [
                { name: this.$t("firmware.selectProduct"), id: 0 },
                { name: this.$t("firmware.selectPlatform"), id: 1 },
                { name: this.$t("firmware.selectVersion"), id: 2 },
                // { name: '下载固件', id: 3 },
            ],
            firmwareProductList1: [],
            firmwareProductList2: [],
            flashinfoData: [],
            versionTab: '0',
            tabIndex: '0',
            firmwareColumn: [
                { name: this.$t("firmware.firmwareColumn1"), property: 'productName', width: '240px', type: 'String' },
                // { name: "产品型号", property: '', width: '200px', type: 'String' },
                { name: this.$t("firmware.firmwareColumn2"), property: 'column2', width: '240px', type: 'String' },
                { name: this.$t("firmware.firmwareColumn3"), property: 'column4', width: '240px', type: 'String' },
                { name: this.$t("firmware.firmwareColumn4"), property: 'column3', width: '240px', type: 'String' },
                // { name: "可否越狱", property: 'prisonBreak', width: '180px', type: 'Boole' },
                { name: this.$t("firmware.firmwareColumn5"), property: 'column1', width: '230px', type: 'Boole' },
            ],
            firmwareTableData: [],
            deviceId:0,

        }
    },
    created() {
        this.getFirmwareData()
    },

    methods: {
        async getFirmwareData() {
            this.active = 1
            console.log(this.$route.query)
            this.deviceId=this.$route.query.productId
            const firmwareProductData = await getFirmwareProduct()
            let obj = firmwareProductData.res.find(item => item.deviceId === Number(this.deviceId));
            console.log(obj,firmwareProductData)
            this.firmwareProductList2 = obj.dataList
        },
        //tab 选中
        activeClick(data) {
            if (this.active > data.id) {
                this.$router.push('/firmware/selectProduct');
            }
        },
        //将图片从base编码转成正常
        imageSrc(data) {
            return `data:image/png;base64, ${data}`;
        },
        //获取设备信息以及下载链接
         platformClick(data) {
            this.$router.push({
                path:"/firmware/selectVersion",
                query: { 
                    deviceId: data.deviceId,
                    productNameid:data.productNameid 
                } 
        })
        },
        //表格点击行
        handleCurrentChange(val) {
            console.log(val)
            this.currentRow = val;
            // this.active = 3

        },
        downloadLink(data) {
            if (!data.downloadLink) return
            let url = data.downloadLink;
            // downloadFile(url)
            // function downloadFile(url) {
            //     //下载文件
            console.log(url)
            let a = document.createElement("a");
            a.setAttribute("href", url);
            a.setAttribute("download", data.column2);
            a.setAttribute("target", "_blank");
            let clickEvent = document.createEvent("MouseEvents");
            clickEvent.initEvent("click", true, true);
            a.dispatchEvent(clickEvent);
            // }
        },
        handleScroll() {
            if (window.pageYOffset > 100) { // 当滚动超过100px时显示“返回顶部”按钮
                this.isShowGoTopButton = true;
            } else {
                this.isShowGoTopButton = false;
            }
        },
        goTop() {
            window.scrollTo({ top: 0, behavior: 'smooth' }) // 平滑滚动至顶部
        },
        updateTitle() {
            this.stepsList = [
                { name: this.$t("firmware.selectProduct"), id: 0 },
                { name: this.$t("firmware.selectPlatform"), id: 1 },
                { name: this.$t("firmware.selectVersion"), id: 2 },
                // { name: '下载固件', id: 3 },
            ]
            this.firmwareColumn = [
                { name: this.$t("firmware.firmwareColumn1"), property: 'productName', width: '240px', type: 'String' },
                // { name: "产品型号", property: '', width: '200px', type: 'String' },
                { name: this.$t("firmware.firmwareColumn2"), property: 'column2', width: '240px', type: 'String' },
                { name: this.$t("firmware.firmwareColumn3"), property: 'column4', width: '240px', type: 'String' },
                { name: this.$t("firmware.firmwareColumn4"), property: 'column3', width: '240px', type: 'String' },
                // { name: "可否越狱", property: 'prisonBreak', width: '180px', type: 'Boole' },
                { name: this.$t("firmware.firmwareColumn5"), property: 'column1', width: '200px', type: 'Boole' },
            ]
        },
    },
    computed: {
        ...mapState(['language']),
    },
    watch: {
        language(newVal, oldVal) {
            console.log('Language changed. Updating bottom title...');
            if (newVal !== oldVal) {
                this.updateTitle();
            }
        },
    },
    mounted() {

        window.addEventListener('scroll', this.handleScroll)
    },
    beforeDestroy() {
        window.removeEventListener('scroll', this.handleScroll);
    },

}
</script>
<style scoped lang="less">
.main {
    height: 100%;
    background-color: #f7f7f7;
    width: 100%;
}

.firmware-function-diagram {
    padding-bottom: 30px;
    min-width: 1200px;
    width: 100%;
    background-color: #FFFFFF;
    // height: 180px;

    .steps {
        width: 900px;
        margin: 0 auto;
        padding-top: 60px;
    }

}

.firmware_Product {
    width: 100%;
    height: auto;
    background-color: #F7F7F7;

    .firmware_Product-Data {
        width: 1200px;
        margin: 40px auto;
        min-height: 58.9vh;
    }

    .firmware_Product-Data3 {
        margin: 40px auto;
        min-height: 58.9vh;
        width: 1400px;
        // margin: 40px auto;
        // height: 58.9vh;
    }

    .firmware_Product-Data-box1 {
        cursor: pointer;
        display: flex;
        width: 1200px;
        justify-content: flex-start;
        flex-wrap: wrap;
        padding-top: 25px;

        .firmware_Product-Data-list:hover {
            border-color: #527fda;
        }


        .firmware_Product-Data-list:hover img {
            transform: scale(1.2);
        }

        .firmware_Product-Data-list {
            width: 268px;
            height: 190px;
            margin: 15px 15px;
            background-color: #fff;
            text-align: center;
            padding: 60px 0 20px 0;
            border-radius: 20px;
            border: 1px transparent solid;
            transition: border-color 0.5s ease;

            img {
                height: 100px;
                transition: transform 0.5s ease;
            }

            .firmware_Product-Data-list-item {
                font-size: 14px;
                margin: 30px 0;
            }
        }
    }

}

/deep/ .el-card__header {
    background-color: #F7F9FC;

}

.clearfix {
    background-color: #F7F9FC;

    .column {
        text-align: center;

    }

    .td_head {
        width: 200px;
    }
}

.myTable {
    border-collapse: collapse;
    margin: 0 auto;
    text-align: center;
}

.myTable td,
.myTable th {
    border: 1px solid #f8fafc;
    padding-left: 20px;
    font-size: 14px;
    color: #666;
    text-align: left;
    height: 50px;
}

.download {
    margin: 0 auto;
    width: 400px;
    height: 43px;
    background-color: #6699CC;
    color: #fff;
    border-radius: 10px;
}

.version-title {
    font-size: 18px;
    font-weight: 600;
    line-height: 40px;
}

.version-header {
    background-color: #F7F9FC;
    padding: 18px 20px;
    box-sizing: border-box;
    // height: 50px;
    // line-height: 50px;
    border-bottom: 1px solid #EBEEF5;

}

/deep/ .el-step__icon-inner {
    display: none;
}

/deep/ .el-step__icon is-text {
    color: #fff;
    background-color: #4E4F54;
}

/deep/ .el-step__head.is-success {
    color: #C0C4CC;
}

/deep/ .el-step__title.is-success {
    color: #C0C4CC;
}

/deep/ .el-step__head.is-success {
    border-color: #C0C4CC;

}

/deep/ .el-step__icon is-text {
    background-color: red;
}

/deep/ .el-tabs__active-bar {
    background-color: #333333;
}

/deep/.el-tabs__item.is-active {
    font-weight: bold;
    color: #333333;
}

/deep/.el-tabs__item.is-active:hover {
    // font-weight: bold;
    color: #333333;
}

/deep/ .el-tabs__item:hover {
    color: #333333;
}


/deep/.el-step__head.is-success {
    margin-left: 20px;
}

/deep/ .el-step__head.is-process {
    // padding-left: 20px;
    margin-left: 20px;

    .el-step__line {
        // margin: 0 50px;
    }

    .el-step__icon {
        background-color: #4E4F54;

    }
}

/deep/ .el-step__head.is-wait {
    padding-left: 20px;
}

/deep/ .el-step__icon {
    background-color: #CCCCCC;
    color: #FFFFFF;
}

/deep/.el-step__head.is-process {
    border-color: #4E4F54;

}

/deep/ .el-step__head.is-wait {
    // border-color: #CCCCCC;
    .el-step__icon {
        background-color: #CCCCCC;
    }
}


.firmware_Product-Data3 {
    /deep/ .el-icon-check {
        font-weight: 600;
        color: #3AA659;
        font-size: 26px;
    }

    /deep/ .el-icon-close {
        font-size: 26px;
        font-weight: 600;
        color: red;
    }
}

.container {
    z-index: 999999999999;
    position: fixed;
    right: 3%;
    bottom: 6%;
    width: 80px;
    height: 80px;
    color: #fff;

    .runter-top {
        width: 80px;
        height: 80px;
    }
}
</style>


